import { message } from 'antd'
import axios from 'axios'
import React, { useEffect, useRef, useState } from 'react'
import { Container, Row, Table } from 'react-bootstrap'
import ReactPaginate from 'react-paginate'

const AllUserContacts = () => {
  const [userDetails, setUserDetails] = useState([])
  const limit = 8
  const [pageCount, setPageCount] = useState(1)
  const currentPage = useRef(1)
  const getPaginatedData = async (page) => {
    try {
      const res = await axios.get(
        `/api/v1/masterAdmin/getUserDetails?page=${currentPage.current}&limit=${limit}`,
      )
      if (res.data.success) {
        const data = res.data.data
        if (data.results.length === 0) {
          setUserDetails([])
        } else {
          setUserDetails(data.results)
          setPageCount(data.totalPages) // Use total pages from the response
        }
      }
    } catch (error) {
      console.log(error)
      message.error('Something went wrong')
    }
  }

  const handlePageClick = (e) => {
    const selectedPage = e.selected + 1
    currentPage.current = selectedPage
    getPaginatedData(selectedPage) // Fetch data for the selected page
  }
  useEffect(() => {
    currentPage.current = 1
    getPaginatedData()
  }, [])

  return (
    <>
      <Row className="mb-3">
        <Container fluid>
          <h3>Contact Details of users</h3>
          <Row>
            <Table responsive>
              <thead>
                <tr>
                  <th className="text-center">Sn</th>
                  <th className="text-center">Name</th>
                  <th className="text-center">UniqueId</th>
                  <th className="text-center">Mobile Number</th>
                  <th className="text-center">Pan No.</th>
                  <th className="text-center">State</th>
                  <th className="text-center">City</th>
                </tr>
              </thead>
              <tbody>
                {userDetails && userDetails.length > 0 ? (
                  userDetails.map((item, index) => (
                    <tr key={index}>
                      <td className="text-center">{index + 1}</td>
                      <td className="text-center">{item.name}</td>
                      <td className="text-center">{item.uniqueId}</td>
                      <td className="text-center">{item.mobileNumber}</td>
                      <td className="text-center">{item.panCard}</td>
                      <td className="text-center">{item.state}</td>
                      <td className="text-center">{item.city}</td>
                    </tr>
                  ))
                ) : (
                  <tr>
                    <td colSpan={8} className="text-center text-danger">
                      No data found
                    </td>
                  </tr>
                )}
              </tbody>
            </Table>
          </Row>
        </Container>
      </Row>
      <Row>
        <ReactPaginate
          breakLabel="..."
          nextLabel="next >"
          onPageChange={handlePageClick}
          pageRangeDisplayed={5}
          pageCount={pageCount}
          previousLabel="< previous"
          renderOnZeroPageCount={null}
          marginPagesDisplayed={2}
          containerClassName="pagination justify-content-center"
          pageClassName="page-item"
          pageLinkClassName="page-link"
          previousClassName="page-item"
          previousLinkClassName="page-link"
          nextClassName="page-item"
          nextLinkClassName="page-link"
          activeClassName="active"
        />
      </Row>
    </>
  )
}

export default AllUserContacts
